
.lgs-alert {
  border: 1px solid #cccccc;
  position: relative;
}
.lgs-alert .close {
  position: absolute;
  top: 0px;
  right: 0px;
  margin-right: 1em;
  margin-top: 2em;
}
.lgs-alert .btn-close {
  color: #000000;
  height: 20px;
  width: 20px;
}
.flyin {
  position: fixed;
  top: 50px;
  right: -10000px;
  width: 400px;
  z-index: 1000;
  animation: slide 0.5s forwards;
  animation-delay: 0.25s;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
}
@keyframes slide {
100% { right: 10px;
}
}




